.SupportUkraineBody {
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, var(--purple-accent-color) 18.03%, #fff 100%);
  border-radius: 16px;
  flex-direction: column;
  max-width: 335px;
  height: 100vh;
  max-height: 460px;
  padding-left: 40px;
  font-family: DM Sans, sans-serif;
  display: flex;
  position: relative;
  box-shadow: 0 10px 10px #f5f5f71a;
}

.supportUkraineTitle {
  color: var(--background-light);
  letter-spacing: -.88px;
  gap: 12px;
  padding: 26px 12px 30px 0;
  font-size: 22px;
  font-weight: 700;
  line-height: 24px;
  display: flex;
}

.numberedList {
  flex-flow: column wrap;
  justify-content: flex-start;
  gap: 20px;
  width: 100%;
  padding: 0;
  list-style: none;
  display: flex;
}

.supportUkraineNumber {
  color: var(--background-light);
  letter-spacing: -.56px;
  padding-right: 16px;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}

.numberedItem {
  align-items: center !important;
}

.numberedItem img {
  filter: brightness(0) invert();
  max-width: 159px;
  max-height: 32px;
  margin-left: 16px;
}

.supportUkraineButton {
  fill: var(--background-main);
  border: 0;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  margin-left: auto;
  margin-right: auto;
  transition: transform .3s;
  display: flex;
  position: absolute;
  bottom: 12px;
  left: 50%;
}

.supportUkraineButton:active {
  transform: scale(.9);
}

.supportUkrainearrow {
  fill: var(--hoover-color-main);
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (width >= 768px) and (width <= 1024px) {
  .SupportUkraineBody {
    max-width: 335px;
    max-height: 474px;
  }

  .supportUkraineTitle {
    letter-spacing: -.96px;
    padding: 26px 12px 26px 0;
    font-size: 24px;
    line-height: 28px;
  }

  .supportUkraineButton {
    width: 38px;
    height: 38px;
    padding-top: 20px;
  }
}

